<template>
<div>
  <div class="player-content" v-if="NewsongDetail">
    <div class="singerpic" @click="showcovers">
      <img :src="NewsongDetail.pic" alt=""/>
    </div>
    <div class="songcontent">
      <div class="song-name">{{NewsongDetail.songName}}</div>
      <div class="singer-name">{{NewsongDetail.singerName}}</div>
    </div>
    <div class="player">
      <audio :src="Newsonglist" autoplay="autoplay" controls="controls" ref="myaudio"></audio>
      <div class="playmusic" @click="playmusic" v-show="playshow">
        <img src="../assets/playstop.png" alt="" />
      </div>
      <div class="stopmusic" @click="stopmusic" v-show="!playshow">
        <img src="../assets/playmove.png" alt="" />
      </div>
      <div class="prev" @click="prevsong">
        <img src="../assets/playleft.png" alt="" />
      </div>
      <div class="next" @click="nextsong">
        <img src="../assets/playright.png" alt="" />
      </div>
    </div>

    <transition name="cover">
      <div class="cover" v-show="showcover">
        <div class="close-cover">
          <span @click="showcoverc">
            <img src="../assets/vvv.png" alt />
          </span>
        </div>

        <div class="bg-singer">
          <img class="mohu" :src="NewsongDetail.pic" alt="" />
          <div class="bg-singer-cover"></div>
        </div>

        <div class="turntable">
          <img
            class="turntablep"
            :class="ismove?'play':'stop'"
            :src="NewsongDetail.pic"
            @click="Lyric"
            alt=""
          />
        </div>

        <BetterScroll ref="lyricList" class="lyric" :data='currentLyric&&currentLyric.line'>
          <ul>
             <li ref='lyricLine' v-for="(item, index) in currentLyric" :key="index"
             :class="{'current':currentLineNum===index}">{{item.txt}}</li>
          </ul>
        </BetterScroll>

        <div class="conbtn">
          <div class="conbtn-play">
             <span class="btn-prev" @click="prevsong">
              <img src="../assets/playleft.png" alt="" />
            </span>
            <span class="btn-playmusic" @click="playmusic" v-show="playshow">
              <img src="../assets/playstop.png" alt="" />
            </span>
           
            <span class="btn-stopmusic" @click="stopmusic" v-show="!playshow">
              <img src="../assets/playmove.png" alt="" />
            </span>
            <span class="btn-next" @click="nextsong">
              <img src="../assets/playright.png" alt="" />
            </span>
          </div>
        </div>
      </div>
    </transition>
  </div>
 </div> 
</template>

<script>
import {Base64} from 'js-base64'
import Lyric from 'lyric-parser'
import BScroll from 'better-scroll'
import BetterScroll from '@/components/BetterScroll.vue'
export default {
  name: "player",
  computed: {
    Newsonglist: function() {
      return this.$store.state.newsonglist;
    },
    NewsongDetail: function() {
      return this.$store.state.newsongdetail;
    },
    NewSongPlayList: function() {
      return this.$store.state.newsongplaylist;
    },
    NewLyric:function(){
      return new Lyric(Base64.decode(this.lyric)).lines
    }
  },
  data() {
    return {
      playshow: false,
      showcover: false,
      isActive: true,
      ismove:true,
      currentLineNum:0,
      currentLyric:null,
      lyric:''
    };
  },
  components:{
    BetterScroll,
    BScroll
  },
  methods: {
    playmusic() {
      this.$refs.myaudio.play();
      this.Lyric()
      this.playshow = false;
      this.ismove=true
    },
    stopmusic() {
      this.$refs.myaudio.pause();
      this.playshow = true;
      this.ismove=false
    },
    showcovers() {
      this.showcover = true;
    },
    showcoverc() {
      this.showcover = false;
    },
    Lyric(){
      const lyric=Base64.decode(this.lyric)
      this.currentLyric=new Lyric(lyric,this.handle).lines
      // console.log(this.currentLyric)
      // if(this.playshow){
      //   console.log('this.playshow',this.playshow)
      //  this.currentLyric.play()
      // }
    },
    handle(lineNum){
      this.currentLineNum=lineNum
      if(lineNum>0){
        let lineEl=this.$refs.lyricLine[lineNum-5]
        this.$refs.lyricList.scrollToElement(lineEl,1000)
      }
      else{
       this.$refs.lyricList.scrollToElement(0,0,2000)
      }

    },
    nextsong() {
      var newsongid = this.Newsonglist;
      var newsonglist = this.NewSongPlayList;
      var currentIndex;
      this.ismove=true
      newsonglist.forEach(function(element, index) {
        if (element.mp3 == newsongid) {
          currentIndex = index;
          console.log(currentIndex);
        }
      });

     
      if (currentIndex > newsonglist.length - 2) {
        currentIndex = -1;
      }
      if (currentIndex < newsonglist.length - 1) {
        this.$store.commit(
          "updataNewsongList",
          newsonglist[currentIndex + 1].mp3
        );
        this.$store.commit(
          "updataNewSongDetail",
          newsonglist[currentIndex + 1]
        );
        console.log(newsonglist[currentIndex + 1]);
      }
    },

    prevsong() {
      var newsongid = this.Newsonglist;
      var newsonglist = this.NewSongPlayList;
      var currentIndex;
      this.ismove=true
      newsonglist.forEach(function(element, index) {
        if (element.mp3 == newsongid) {
          currentIndex = index;
        }
      });
      if (currentIndex < 1) {
        currentIndex = newsonglist.length - 1;
        console.log(currentIndex);
        this.$store.commit(
          "updataNewsongList",
          newsonglist[currentIndex].mp3
        );
        this.$store.commit("updataNewSongDetail", newsonglist[currentIndex]);
      } else {
        this.$store.commit(
          "updataNewsongList",
          newsonglist[currentIndex - 1].mp3
        );
        this.$store.commit(
          "updataNewSongDetail",
          newsonglist[currentIndex - 1]
        );
      }
    }
  },
  watch: {
    Newsonglist(newvalue, oldvalue) {
      if (oldvalue != newvalue) {
        this.playshow = false;
      }
    }
  },
};
</script>

<style lang="scss" scoped>
.player-content {
  position: fixed;
  display: flex;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 40px;
  background: rgba(0, 0, 0, 0.2);
  
  .cover {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 667px;
    z-index: 10;
    background: #fff;
    .lyric{
      position: absolute;
      top: 390px;
      left: 0;
      width: 100%;
      height: 130px;
      background: none;
       overflow: hidden;
      ul{
        padding: 0;
        margin: 0;
        width: 100%;
        // height: 100%;
       
        li{
          font-size: 14px;
          color: white;
          list-style: none;
          &.current{
            color: yellow;
          }
        }
      }
   }
    .close-cover {
      width: 100%;
      height: 30px;
      text-align: left;
      span {
        display: inline-block;
        width: 30px;
        height: 30px;
        text-align: center;
        padding-top: 5px;
        transform: rotateZ(90deg);
        img {
          width: 15px;
          height: 20px;
          background-size: cover;
        }
      }
    }
    .bg-singer {
      width: 100%;
      height: 100%;
      position: relative;
      overflow: hidden;
      .bg-singer-cover {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0,0,0.3);
      }
    }
    .mohu {
      filter: blur(20px);
      width: 100%;
      height: 85%;
    }
    @keyframes run {
      from {
        transform: rotateZ(0deg);
      }
      to {
        transform: rotateZ(360deg);
      }
    }
    .turntable {
      width: 300px;
      height: 300px;
      position: absolute;
      top: 70px;
      left: 50%;
      border-radius: 50%;
      border: 4px solid rgba(255, 255, 255, 0.3);
      transform: translateX(-50%);

      .turntablep {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        animation: run 10s infinite linear;
      }
      // .move {
      //   width: 100%;
      //   height: 100%;
      //   border-radius: 50%;
      //   animation: run 10s infinite linear;
      // }
      .play{
        animation: run 10s infinite linear;
      }
      .stop{
         animation-play-state: paused;
      }
    }
    .conbtn {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 80px;
      .conbtn-play {
        width: 100%;
        height: 100%;
        span {
          display: inline-block;
          width: 50px;
          height: 50px;
          img {
            width: 100%;
            height: 100%;
          }
        }
        // .btn-playmusic {
         
        //   bottom: 120px;
        //   left: 165px;
        // }
        // .btn-stopmusic {
         
        //   bottom: 120px;
        //   left: 165px;
        // }
        // .btn-prev {
          
        //   bottom: 120px;
        //   left: 100px;
        // }
        // .btn-next {
         
        //   bottom: 120px;
        //   left: 230px;
        // }
      }
    }
  }
  .singerpic {
    flex: 0 0 70px;
    width: 40px;
    height: 40px;
    img {
      width: 40px;
      height: 100%;
      border-radius: 50%;
    }
  }
  .songcontent {
    flex: 0 0 170px;
    width: 170px;
    height: 40px;
    .song-name {
      text-align: left;
      font-size: 16px;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .singer-name {
      text-align: left;
      font-size: 14px;
      color: #999;
    }
  }
  .player {
    flex: 0 0 125px;
    width: 125px;
    height: 40px;
    position: relative;
    .playmusic {
      position: absolute;
      top: 0px;
      left: 50%;
      margin-left: -20px;
      width: 40px;
      height: 40px;
      overflow: hidden;
      border-radius: 50%;
      z-index: 10;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .stopmusic {
      position: absolute;
      top: 0px;
      left: 50%;
      margin-left: -20px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .prev {
      position: absolute;
      top: 0px;
      left: 0;
      width: 40px;
      height: 40px;
      overflow: hidden;
      border-radius: 50%;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .next {
      position: absolute;
      top: 0px;
      right: 0;
      width: 40px;
      height: 40px;
      overflow: hidden;
      border-radius: 50%;
      img {
        width: 100%;
        height: 100%;
      }
    }
    audio {
      width: 1%;
      height: 1%;
    }
  }

  .cover-enter,
  .cover-leave-to {
    width: 100%;
    height: 0;
  }
  .cover-enter-active,
  .cover-leave-active {
    transition: all 0.3s;
  }
  .cover-enter-to,
  .cover-leave {
    width: 100%;
    height: 667px;
  }
}
</style>